<template>
  <div class="shopcart page">
    <header>
      <span>购物车</span>
    </header>
    <shop-scroll class="content">
      <ul class="con1-list">
        <li v-for="item in cartData" :key="item._id" >
          <input type="checkbox" class="text" :value="item" v-model="goods "   />
          <div >
            <img :src="item.goodImage" alt="" />
            <div class="list-right" >
              <h1 >{{ item.goodName }}</h1>
              <div>
                <span>￥{{ item.goodPrice }}</span>
                <div class="right1">
                  <span
                    class="iconfont iconarrow-down1"
                    @click="pre(item)"
                  ></span>
                  <span>{{ item.goodSales }}</span>
                  <span
                    class="iconfont iconarrow-down2"
                    @click="next(item)"
                  ></span>
                </div>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </shop-scroll>

    <div class="bottom">
      <div class="bottom-left" >
        <input type="checkbox" class="int"  />
        <span >已选</span>
      </div>

      <div class="bottom-right">
        <span>合计:￥{{ sumMoney }}</span>
        <h1 >购买</h1>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      goods: [],
    };
  },

  computed: {
    ...mapState({
      cartData: (state) => state.shop.cartData,
    }),
    sumMoney() {
      let money = 0;
      console.log(this.goods);
      this.goods.map((item) => {
        money += parseFloat(item.goodPrice) * parseInt(item.goodSales);
      });
      
      return money;
    },
  },

  methods: {
    pre(item) {
      item.goodSales--;
      if (item.goodSales < 1){
        item.goodSales = 1;
        
      }else{
        this.$store.dispatch("shop/requestFlagInfo", { item, type: "pre" });
      }
      

      
    },
    next(item) {
      item.goodSales++;
      this.$store.dispatch("shop/requestFlagInfo", { item, type: "next" });
    },
    // add(){
    //   this.goods = this.cartData;
    // }   
   
  },
  created() {
    this.$store.dispatch("shop/requestShopcartInfo");
  },
};
</script>

