  top: 0px;
  left: 0;
  bottom: 59px;
  width: 100%;
  // background-color: greenyellow;
  .con1-list {
    width: 100%;
    display: flex;
    flex-direction: column;
    li {
      width: 375px;
      height: 134px;
      display: flex;
      align-items: center;
      .text {
        width: 43px;
      }
      div {
        display: flex;
        justify-content: flex-start;
        img {
          width: 86px;
          height: 86px;
        }
        .list-right {
          display: flex;
          padding-left: 10px;
          width: 200px;
          flex-direction: column;
          justify-content: space-between;
          // align-items: center;
          h1 {
            font-size: 16px;
            height: 18px;
            line-height: 18px;
          }
          div {
            display: flex;
            justify-content: space-between;
            height: 20px;
            align-items: center;
            span {
              font-size: 14px;
              color: red;
            }
            .right1 {
              span {
                color: #333;
                border: 1px solid #ccc;
                width: 20px;
                text-align: center;
                line-height: 18px;
                height: 18px;
              }
            }
          }
        }
      }
    }
  }
}
.bottom {
  border-top: 1px solid #ccc;

  display: flex;
  height: 59px;
  box-sizing: border-box;
  width: 100%;
  padding: 0 0 0 15px;
  justify-content: space-between;
  align-items: center;
  // background-color: hotpink;
  position: absolute;
  bottom: 0px;
