  .bottom-left {
    .int {
      padding: 0 3px;
      margin-top: 2px;
    }
    span {
      font-size: 14px;
      color: #333;
    }
  }
  .bottom-right {
    display: flex;
    align-items: center;
    span {
      font-size: 14px;
      color: #dd1a21;
      padding-right: 20px;
    }
    h1 {
